import React, { useState } from 'react';
import SearchBar from '../components/SearchBar';
import ResultsTable from '../components/Certificate.table';
import PrintPreviewModal from '../components/PrintPreview.modal';
import UpdateForm from '../components/UpdateForm';
import axios from 'axios';
import { Certificate } from '@/types/Certificate.type';
import { useNavigate } from 'react-router-dom';

const MainPage: React.FC = () => {
  const [results, setResults] = useState<Certificate[]>([]);
  const [selectedRecord, setSelectedRecord] = useState<Certificate | null>(null);
  const [printData, setPrintData] = useState<Certificate[] | null>(null);
  const navigate = useNavigate();

  const handleSearch = async (filterType: string, searchValue: string) => {
    try {
      const response = await axios.get<Certificate[]>(`/api/cert/`, {
        params: {
          filter_type: filterType,
          search_value: searchValue
        }
      });
      setResults(response.data);
    } catch (error) {
      console.error(" :", error);
      alert("   ");
    }
  };

  const handleUpdate = async (filter: object, updateData: object) => {
    try {
      const response = await axios.put(`/api/cert/update/`, { filter, update_data: updateData });
      alert(response.data.message);
    } catch (error) {
      console.error(" :", error);
      alert("   ");
    }
  };

  const handlePrintPreview = () => {
    setPrintData(results);
  };

  const goToSearchPage = () => {
    navigate('/search');
  };

  return (
    <div className="app-container">
      <h1>  </h1>
      <SearchBar onSearch={handleSearch} />
      <ResultsTable results={results} onSelect={setSelectedRecord} />
      <UpdateForm selectedRecord={selectedRecord} onUpdate={handleUpdate} />
      <div style={{ marginTop: '20px' }}>
        <button onClick={handlePrintPreview}> </button>
        <button onClick={goToSearchPage} style={{ marginLeft: '10px' }}> </button>
      </div>
      {printData && <PrintPreviewModal data={printData} />}
    </div>
  );
};

export default MainPage;