import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { fetchCertificates } from '../services/certificate.api';
import { Certificate } from '../types/Certificate.type';
import CertificateTable from '../components/Certificate.table';
import { Input } from '../components/ui/input';
import { Button } from '../components/ui/button';
import { Select } from '../components/ui/select';

const SearchPage: React.FC = () => {
  const [filterType, setFilterType] = useState('user_name');
  const [searchValue, setSearchValue] = useState('');
  const [results, setResults] = useState<Certificate[]>([]);
  const navigate = useNavigate();

  const handleSearch = async () => {
    try {
      const response = await fetchCertificates({
        filter_type: filterType,
        search_value: searchValue,
      });
      setResults(response);
    } catch (error) {
      console.error(' :', error);
      alert('   ');
    }
  };

  const handleBack = () => {
    navigate('/');
  };

  return (
    <div className="p-6 max-w-4xl mx-auto">
      <h2 className="text-2xl font-bold mb-4"> </h2>
      <div className="flex items-center gap-4 mb-6">
        <div>
          <label htmlFor="search-filter" className="block text-sm font-medium mb-1"></label>
          <select
            id="search-filter"
            value={filterType}
            onChange={(e) => setFilterType(e.target.value)}
            className="border rounded p-2"
          >
            <option value="user_name"></option>
            <option value="education_center"></option>
            <option value="phone_number"></option>
            <option value="issue_number"></option>
          </select>
        </div>
        <div className="flex-1">
          <label htmlFor="search-input" className="block text-sm font-medium mb-1"></label>
          <Input
            id="search-input"
            type="text"
            placeholder=" "
            value={searchValue}
            onChange={(e) => setSearchValue(e.target.value)}
          />
        </div>
        <div className="flex gap-2 pt-5">
          <Button variant="outline" onClick={handleSearch}></Button>
        </div>
      </div>

      <CertificateTable results={results} />
    </div>
  );
};

export default SearchPage;
